<template>
  <Select
    v-bind="$attrs"
    v-model="lang"
    :options="selectOptions"
    hide-on-click
    :show-arrow="showArrow"
    class="lang"
  >
    <template #selected>
      <div class="lang__selected">
        <Icon :name="lang.label"/>
      </div>
    </template>
    <template v-slot:option="{ option }">
      <div class="d-flex justify-content-center align-items-center">
        <Icon :name="option.label" />
        <Text type="uppercase" ml="8px" weight="500" size="14px">{{ option.value }}</Text>
      </div>
    </template>
  </Select>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref, watch } from 'vue'
import moment from 'moment'
import Icon from '@/components/Icon/Icon'
import Select from '@/components/UIKit/Select/Select'
import Text from '@/components/Text/Text'

export default {
  name: 'Lang',
  inheritAttrs: true,
  components: { Text, Select, Icon },
  props: {
    showArrow: {
      type: Boolean,
      default: false
    }
  },
  setup () {
    const { locale } = useI18n()

    const selectOptions = ref([
      {
        label: 'us',
        value: 'en'
      },
      {
        label: 'ru',
        value: 'ru'
      },
      {
        label: 'cn',
        value: 'cn'
      }
    ])

    const lang = ref({
      label: 'us',
      value: 'en'
    })

    watch(lang, nv => {
      moment.locale(nv.value)
      locale.value = nv.value
    })

    return {
      lang,
      selectOptions
    }
  }
}
</script>

<style lang="scss">
.lang {
  margin-left: 14px;

  &__selected {
    width: 20px;
  }
  .select__options {
    width: unset;

    &__item {
      box-sizing: border-box;
      border: 2px solid transparent;
      border-bottom: 1px solid $borderColor;
      padding: 10px 16px;

      &_selected {
        border-bottom: 2px solid map-get($colors, "primary");
        border-color: map-get($colors, "primary") !important;
        background: #FFF3EA;
      }
    }
  }
}
</style>
