<template>
  <Container
    v-bind="$attrs"
    background-color="white"
    class="select"
    v-click-outside="hideDropdown"
  >
    <div class="select__body" @click="showDropdown = !showDropdown">
      <slot name="selected" />
      <Icon v-if="showArrow" name="chevron-down" class="select__body__arrow" />
    </div>
    <Container
      box-shadow
      background-color="white"
      v-if="showDropdown"
      class="select__options"
      :class="'select__options-' + dropdownSide"
    >
      <div
        v-for="item in filteredOptions"
        :key="item.value"
        class="select__options__item"
        :class="{ select__options__item_selected: item.value === modelValue.value }"
        @click="handleChange(item)"
      >
        <slot name="option" :option="item" />
      </div>
    </Container>
  </Container>
</template>

<script>
import { ref, computed } from 'vue'
import Icon from '@/components/Icon/Icon'
import Container from '@/components/Container'
import clickOutside from '@/directives/clickOutside'
export default {
  name: 'Select',
  components: { Container, Icon },
  inheritAttrs: true,
  emits: ['update:modelValue'],
  props: {
    options: {
      type: Array,
      default: () => []
    },
    modelValue: {
      type: Object,
      default: () => ({})
    },
    showArrow: {
      type: Boolean,
      default: false
    },
    dropdownSide: {
      type: String,
      default: 'top'
    },
    isFilterOption: {
      type: Boolean,
      default: false
    },
    hideOnClick: {
      type: Boolean,
      default: false
    }
  },
  directives: {
    clickOutside
  },
  setup (props, { emit }) {
    const showDropdown = ref(false)
    const hideDropdown = () => {
      showDropdown.value = false
    }

    const handleChange = (item) => {
      if (props.hideOnClick) {
        hideDropdown()
      }
      selectedItem.value = item
    }

    const selectedItem = computed({
      get () {
        return props.modelValue
      },
      set (val) {
        emit('update:modelValue', val)
      }
    })

    const filteredOptions = computed(() => {
      if (props.isFilterOption) {
        return props.options.filter(item => item.value !== props.modelValue.value)
      }
      return props.options
    })

    return {
      showDropdown,
      filteredOptions,
      hideDropdown,
      handleChange
    }
  }
}
</script>

<style lang="scss">
.select {
  position: relative;
  cursor: pointer;

  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 9px;

    &__arrow {
      margin-left: 11px;
    }
  }

  &__options {
    position: absolute;
    z-index: 10;
    width: 100%;

    &-top {
      left: 0;
      bottom: calc(100% + 3px);
    }
    &-bottom {
      right: 0;
      top: calc(100% + 3px);
    }
    &__item {
      display: flex;
      justify-content: center;
      padding: 8px 19px;

      $borderRadiusItem: 6px;

      &:last-child {
        border-bottom-right-radius: $borderRadiusItem;
        border-bottom-left-radius: $borderRadiusItem;
      }
      &:first-child {
        border-top-left-radius: $borderRadiusItem;
        border-top-right-radius: $borderRadiusItem;
      }

      &:nth-of-type(2n - 1) {
        border-bottom: 1px solid $borderColor;
      }
    }
  }
}
</style>
